.inputPage {
    display: flex;
    background:#dfdfdf;
    width: 100vw;
    height: 100vh;
    align-items: center;
    justify-content: center;
}
.windowInput {
    text-align: center;
   width: 900px;
   height: 400px;
   background: #ffffff;
   border-radius: 50px;
}
.title {
    margin-top: 41px;
   font-size: 36px;
}
.fieldInputUser {
    margin: 0 auto;
}
.inputData {
    width: 500px;
   height: 42px;
   margin-top: 30px;
   border-radius: 16px;
   padding: 0 20px;
   letter-spacing: 1.5px;
   border: none;
   box-shadow: inset 0px 3px 10px 0px #cfcfcf;
}
.error {
    font-size: 12px;
   color: #b72025;
   margin-top: 5px;
}
.submit {
    margin: 20px 0;
   width: 300px;
   height: 42px;
   background: #15499f;
   border: none;
   border-radius: 16px;
   cursor: pointer;
   color: #fff;
   font-size: 18px;
   letter-spacing: 1.5px;
}